
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
  /*background-color: #f7f7f7;*/
}
.lightbox {
  top:0px;
  position: fixed;
  background: rgba(0,0,0,.4);
  width: 100%;
  height: 100%;
  z-index:10;
}
html, body {
  margin: 0px;
  padding: 0px;
}



