
.markdown-text a {
  color: #08c;
  word-break: break-word;
  text-decoration: none;
}
.markdown-text pre {
  font-size: 95%;
  line-height: 140%;
  white-space: pre-wrap;
  background-color: #f7f7f7;
}
.markdown-text pre code {
    font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
    font-size: 95%;
    line-height: 140%;
    white-space: pre-wrap;
    display: block;
    font-weight: lighter;
    padding: .5em 1em;
    overflow: scroll;
}
.markdown-text img {
  max-width: 100%;
}
.markdown-text li {
  font-weight: inherit;
}
.markdown-text p {
  word-break: break-word;
}
.markdown-text table {
  display: table;
  max-width: 100%;
  border-collapse: separate;
  border-spacing: 0px;
  border-color: grey;
  border-width: .1px;
  border-style: solid;
}
.markdown-text table td, .markdown-text table th {
    word-break: break-word;
    border-spacing: 0;
    border-width: .1px;
    border-style: solid;
}



