
.notification {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  position: fixed;
  width: 100%;
  height: 50px;
  top: 0px;
  -webkit-box-shadow: 1px 1px 1px #888888;
          box-shadow: 1px 1px 1px #888888;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 80;
  color: white;
}
.notification div:first-of-type {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.notification div:first-of-type span {
      font-size: 15px;
      margin-left: 50px;
      margin-right: 20px;
}
.notification div:first-of-type div {
      margin-left: 20px;
}
.animate-enter-active {
  -webkit-transition: top .5s;
  transition: top .5s;
}
.animate-leave-active {
  -webkit-transition: top 1s;
  transition: top 1s;
}
.animate-enter, .animate-leave-to {
  top: -80px;
}



